import React from "react";
import {
    Row,
    Col,
    Card,
    InputGroup,
    FormControl,
    Button,
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import Checkbox from '@material-ui/core/Checkbox';
import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email(i18n.t('error-email')).required(i18n.t('error-required-email')),
    phone_number: Yup.string()
          .matches(phoneRegExp,i18n.t('error-phone-number'))
          .required(i18n.t('error-required-phone-number')),
          firstname: Yup.string()
          .min(2, i18n.t('error-short-first-name'))
          .required(i18n.t('error-required-first-name')),
      lastname: Yup.string()
          .min(2,i18n.t('error-short-last-name'))
          .required(i18n.t('error-required-last-name')),
      address: Yup.string()
          .required(i18n.t('error-address')),
      username: Yup.string()
          .required(i18n.t('error-username')),
      password: Yup.string()
          .required(i18n.t('error-password')),
});
class AddStorekeeper extends React.Component  {
    state = {

        privileges: [],
        permissions: [],
        agences: [],
        permissionss:[],
        showMessageParams:{
            display:false,
            type:"success",
            message:null,                
        },

    };
    handleChange = (event) => {
        console.log(event.target.value);
        this.setState({ permissionss: [...this.state.permissionss, event.target.value]})
    };
    componentDidMount() {
        // axios.get(urlbase+"/privilege/list").then(response=>{


        //   console.log(response.data)
        // })
        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Headers': 'Authorization',
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };

        var getprivileges = axios.get(urlbackend + "/privileges", config)
        var getagence = axios.get(urlbackend + "/agence", config)
        var getpermissions = axios.get(urlbackend + "/permissions", config)


        axios
            .all([getprivileges, getagence, getpermissions])
            .then(
                axios.spread((...response) => {
                    console.log("this respon list0", response[2].data)
                    console.log("this respon list1", response[1].data)
                    this.setState({ privileges: response[0].data.data })
                    this.setState({ agences: response[1].data.data })
                    this.setState({ permissions: response[2].data.data })


                })
            )
            .catch(errors => {
                // react on errors.
                console.error(errors);
            });

    }

    handlSubmit = (values) => {
        this.setState({ 
            showMessageParams:{
                display:false,
                type:"success",
                message:null
                
            }
        });
        values.permissions = this.state.permissionss
        console.log(values);


        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Headers': 'Authorization',
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };

        axios.post(urlbackend + "/addUser/magazinier", values, config)
            .then(response => {
                this.setState({ 
                    showMessageParams:{
                        display:true,
                        type:"success",
                        message:i18n.t('success-add-store-keeper')
                    }
                });
            })
            .catch(e => {
                this.setState({ 
                    showMessageParams:{
                        display:true,
                        type:"danger",
                        message:i18n.t('error-add-store-keeper')
                    }
                });
                console.log(e)
            })
    };
    showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        const { showMessageParams } = this.state;
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">{i18n.t('add-store-keeper')}</Card.Title>
                            </Card.Header>
                            {showMessageParams.display==true ?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null
                            }
                            <Card.Body>
                                <Row>
                                    <Col md={6}>
                                        <CustomInput
                                            name="firstname"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.firstname}
                                            errors={errors.firstname}
                                            touched={touched.firstname}
                                            label={i18n.t('first-name')}
                                        />

                                        <CustomInput
                                            name="lastname"
                                            label={i18n.t('last-name')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.lastname}
                                            errors={errors.lastname}
                                            touched={touched.lastname}
                                        />
                                        <CustomInput
                                            name="username"
                                            label={i18n.t('username')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.username}
                                            errors={errors.username}
                                            touched={touched.username}
                                        />

                                        <CustomInput
                                            name="password"
                                            label={i18n.t('password')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.password}
                                            errors={errors.password}
                                            touched={touched.password}
                                            type="password"
                                        />
                                        <CustomInput
                                            name="email"
                                            label={i18n.t('email')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.email}
                                            errors={errors.email}
                                            touched={touched.email}
                                        />

                                        <div>

                                            {this.state.permissions.map((e) => (
                                                <div>
                                                    <Checkbox
                                                        onChange={(e) => this.handleChange(e)}
                                                        inputProps={{ 'aria-label': 'primary checkbox' }}
                                                        value={e.id}
                                                        key={e.id}
                                                    />  {e.name} 
                                                </div>
                                            ))}

                                        </div>

                                    </Col>
                                    <Col md={6}>
                                        <CustomInput
                                            name="phone_number"
                                            label={i18n.t('phone-number')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.phone_number}
                                            errors={errors.phone_number}
                                            touched={touched.phone_number}
                                        />
                                        <CustomInput
                                            name="address"
                                            label={i18n.t('address')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.address}
                                            errors={errors.address}
                                            touched={touched.address}
                                        />
                                        <label htmlFor="basic-url">{i18n.t('descriminator')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="descriminator"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.Formateur}
                                            >
                                                <option>{i18n.t('choose-descriminator')}</option>

                                                {[
                                                    `regular_user`,
                                                    `magasinier`,
                                                ].map((descriminator, index) => (
                                                    <option key={descriminator} value={descriminator}>
                                                        {descriminator}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                        <label htmlFor="basic-url">{i18n.t('privilege')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="privilege"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.Formateur}
                                            >
                                                <option>{i18n.t('choose-privilege')}</option>

                                                {this.state.privileges.map((privilege, index) => (
                                                    <option key={privilege.id} value={privilege.id}>
                                                        {privilege.name} :{privilege.description}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                        <label htmlFor="basic-url">{i18n.t('agency')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="agence"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.Formateur}
                                            >
                                                <option>{i18n.t('choose-agency')}</option>

                                                {this.state.agences.map((agence, index) => (
                                                    <option key={agence.id} value={agence.id}>
                                                        {agence.name} :{agence.address}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                    </Col>
                                </Row>


                                <Button
                                disabled={Object.keys(errors).length>0}
                                    onClick={() => {
                                        this.handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };

    render() {
        return (
            <Formik
                initialValues={{
                    firstname: "",
                    lastname: "",
                    username: "",
                    password: "",
                    email: "",
                    phone_number: "",
                    address: "",
                    descriminator:"",
                    agence:"",
                    privilege:""
                }}
                onSubmit={(values, { setSubmitting }) => {
                    this.submitForm(values);
                    setSubmitting(false);
                }}
                validationSchema={SignupSchema}
            >
                {(props) => this.showForm(props)}
            </Formik>
        );
    }
}

export default AddStorekeeper;
